
  import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'

  import { mapGetters, mapActions } from 'vuex'
  export default {
    components: {
      Flexbox,
      FlexboxItem,
      Scroller,
      Spinner
    },
    data () {
      return {
        pageid: 1,
        records: [],
        status: {
          pulldownStatus: 'default',
          pullupStatus: 'default'
        },
        upConf: {
		  content: 'Pull Up To Refresh',
		  pullUpHeight: 60,
		  height: 40,
		  autoRefresh: false,
		  downContent: '上拉加载',
		  upContent: '加载中...',
		  loadingContent: '加载中...',
		  clsPrefix: 'xs-plugin-pullup-'
		}
      }
    },
    computed: {
      ...mapGetters({
        ShiftsStatis :'QueryShiftsStatis'
      }),
      params() {
        return {
          pageid: 1,
          fromDate: this.$store.state.statisParams.fromDate,
          toDate: this.$store.state.statisParams.toDate
        }
      }
    },
    methods: {
      ...mapActions([
        'QueryShiftsStatis'
      ])
    },
    watch: { // 监听下拉下拉的状态
      status (e) {
        const $this = this
        if (e.pullupStatus === 'loading') { //上拉加载
          setTimeout(() => {
            this.params.pageid++
            $this.QueryShiftsStatis(this.params).then((res) => {
              this.records = this.records.concat(this.ShiftsStatis)
              $this.$nextTick(() => {
		        this.$refs.scroller.reset()
		      })
              $this.status.pullupStatus = 'default'
            })
          }, 1000)
        }
      }
    },
    created () {
      this.QueryShiftsStatis(this.params).then((res) => {
      	this.records = this.ShiftsStatis
      	if(this.records.length < 15) {
          this.status.pullupStatus = 'disabled'
        }
        this.$nextTick(() => {
          this.$refs.scroller.reset()
        })
      })
    }
  }



